// Notepad
// Task bar

#game-page {
    position: relative;

    header {
        position: relative;

        .logo {
            position: fixed;
            z-index: 15;
            padding: 0.8%;
            right: 100%;
            width: 3%;
            border-radius: 19.5%;
            margin: 0.5%;
            // Glassmorphism
            background-image: -webkit-linear-gradient($white-degraded);
            background-image: -moz-linear-gradient($white-degraded);
            background-image: -o-linear-gradient($white-degraded);
            background-image: linear-gradient($white-degraded);
        }

        .mute {
            position: fixed;
            bottom: 3%;
            right: 2%;
        }
    }

    #Game {
        position: relative;
        width: 100%;
        height: 100%;

        #bg {
            min-height: 100%;
            width: 100%;
            height: 100%;
            z-index: -4;
        }

        #Objects div,
        #Decors div {
            img {
                position: absolute;
                bottom: 0;
                height: auto;
                background-size: cover;
                -webkit-transition: .3s ease-in-out;
                -o-transition: .3s ease-in-out;
                transition: .3s ease-in-out;
            }
        }

        #Objects div {
            -webkit-transition: .3s ease-in-out;
            -o-transition: .3s ease-in-out;
            transition: .3s ease-in-out;
        }

        #Decors img {
            pointer-events: none; // Empecher la selection d'un objet, sauf que le hover fonctionne plus après
        }
    }
}
